"use client";

import { useEffect, useState } from "react";
import { CheckCircle, Power, Lock } from "lucide-react";

export default function ModuleGrid() {
  const [modules, setModules] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch("/api/modules")
      .then((res) => res.json())
      .then((data) => {
        setModules(data);
        setLoading(false);
      });
  }, []);

  async function toggleModule(moduleKey) {
    await fetch("/api/modules", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ moduleKey }),
    });

    const refreshed = await fetch("/api/modules").then((r) => r.json());
    setModules(refreshed);
  }

  if (loading) {
    return (
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
        {[...Array(3)].map((_, i) => (
          <div
            key={i}
            className="h-40 rounded-xl bg-zinc-900/50 animate-pulse border border-zinc-800"
          />
        ))}
      </div>
    );
  }

  if (!modules.length) {
    return (
      <div className="text-center text-zinc-400 py-12">
        No hay módulos disponibles.
      </div>
    );
  }

  return (
    <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
      {modules.map(({ module, active }) => (
        <div
          key={module.id}
          className="relative rounded-xl border border-zinc-800 bg-zinc-950 p-6 flex flex-col justify-between shadow-sm hover:border-zinc-700 transition"
        >
          {/* Badge */}
          <div className="absolute top-4 right-4">
            {active ? (
              <span className="flex items-center gap-1 text-xs font-medium text-emerald-400">
                <CheckCircle size={14} />
                Activo
              </span>
            ) : module.type === "pro" ? (
              <span className="flex items-center gap-1 text-xs font-medium text-amber-400">
                <Lock size={14} />
                PRO
              </span>
            ) : (
              <span className="text-xs text-zinc-500">Inactivo</span>
            )}
          </div>

          {/* Content */}
          <div>
            <h3 className="text-lg font-semibold text-white mb-1">
              {module.name}
            </h3>
            <p className="text-sm text-zinc-400 leading-relaxed">
              {module.description}
            </p>
          </div>

          {/* Action */}
          <div className="pt-4">
            <button
              onClick={() => toggleModule(module.key)}
              className={`w-full inline-flex items-center justify-center gap-2 rounded-md px-4 py-2 text-sm font-medium transition
                ${
                  active
                    ? "bg-zinc-800 text-zinc-200 hover:bg-zinc-700"
                    : "bg-indigo-600 text-white hover:bg-indigo-500"
                }
              `}
            >
              <Power size={16} />
              {active ? "Desactivar módulo" : "Activar módulo"}
            </button>
          </div>
        </div>
      ))}
    </div>
  );
}
